.container {
  display: flex;
  justify-content: center;
  padding: 40px 20px;
  gap: 30px;
  background: var(--bg-color);
  min-height: 100vh;
  flex: 1;
}

.main-content {
  width: 65%;
  background: var(--card-bg);
  border-radius: 15px;
  padding: 20px;
  box-shadow: var(--shadow);
  color: var(--text-color);
  font-family: 'Poppins', sans-serif;
}

.main-content h1 {
  font-size: 2.2rem;
  margin-bottom: 15px;
  color: var(--text-color); /* Ensure visibility */
  font-weight: 600;
}

.main-content h2 {
  font-size: 1.8rem;
  margin-bottom: 15px;
  color: var(--text-color); /* Black in light, white in dark */
  font-weight: 600;
}

.divider {
  border: 0;
  height: 2px;
  background: var(--accent-color);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  margin-bottom: 25px;
}

.mode-divider {
  border: 0;
  height: 2px;
  background: var(--text-color); /* Default to text color */
}

[data-theme="light"] .mode-divider {
  background: #d35400; /* Brown in light mode */
}

[data-theme="dark"] .mode-divider {
  background: #fcd46c; /* Yellow in dark mode */
}

.main-content p {
  font-size: 1.1rem;
  line-height: 1.8;
  color: var(--text-color);
}

.main-content strong {
  font-weight: 600;
}

.main-content ul {
  list-style-type: disc;
  padding-left: 20px;
  font-size: 1.1rem;
}

.main-content form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.main-content label {
  display: flex;
  flex-direction: column;
  font-size: 1.1rem;
}

.main-content input,
.main-content textarea {
  margin-top: 5px;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid var(--accent-color);
  background: var(--bg-color);
  color: var(--text-color);
  font-size: 1rem;
}

.main-content button {
  padding: 10px 20px;
  background: var(--accent-color);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem; /* Reduced size for neatness */
  font-weight: 500;
  width: fit-content;
  transition: background-color 0.3s ease, transform 0.2s ease, box-shadow 0.2s ease;
}

[data-theme="light"] .main-content button {
  background: #d35400; /* Brown in light mode */
  color: #ffffff;
}

[data-theme="dark"] .main-content button {
  background: #fcd46c; /* Yellow in dark mode */
  color: #000000;
}

.main-content button:hover {
  background: #e67e22; /* Warm orange for light mode, smoother transition */
  color: #ffffff;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

[data-theme="dark"] .main-content button:hover {
  background: #f1c40f; /* Brighter yellow in dark mode for contrast */
  color: #000000;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
    padding: 20px;
    gap: 20px;
  }
  .main-content {
    width: 100%;
  }
  .skills {
    flex-direction: column;
  }
  .skill-card {
    width: 100%;
  }
}